.Home .banner {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center; }
  .Home .banner img {
    width: 100%;
    height: 300px;
    object-fit: cover; }
  .Home .banner .site-title {
    position: absolute;
    bottom: 100px;
    color: #fff; }

.Home .entries {
  background-color: #fff;
  position: relative;
  top: -30px; }
  .Home .entries .entry {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center; }
  .Home .entries .icon {
    width: 150px;
    height: 150px; }
